.navbar-default {
    background-color: $navbar-default-bg;
    border-color: $navbar-default-border;
    
    $shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
    @include box-shadow($shadow);
    
    & .navbar-brand {
        color: $navbar-default-link-color;
        
        &:hover {
            color: $navbar-default-link-hover-color;
        }
        
        &:focus {
            color: $navbar-default-link-active-color;
        }
    }
    
    & .navbar-text {
        color: $navbar-default-link-color;
    }
    
    & .navbar-nav > li {
        & > a {
            color: $navbar-default-link-color;
            
            &:hover {
                background-color: $navbar-default-link-hover-bg;
                color: $navbar-default-link-hover-color;
            }
            
            &:focus {
                background-color: $navbar-default-link-active-bg;
                color: $navbar-default-link-active-color;
            }
        }
        
        &.active > a {
            background-color: $navbar-default-link-active-bg;
            color: $navbar-default-link-active-color;
            
            &:hover,
            &:focus {
                background-color: $navbar-default-link-active-bg;
                color: $navbar-default-link-active-color;
            }
        }
        
        &.open > a {
            background-color: $navbar-default-link-active-bg;
            color: $navbar-default-link-active-color;
            
            &:hover,
            &:focus {
                background-color: $navbar-default-link-active-bg;
                color: $navbar-default-link-active-color;
            }
        }
        
        &.disabled > a {
            background-color: $navbar-default-link-disabled-bg;
            color: $navbar-default-link-disabled-color;
            
            &:hover {
                background-color: $navbar-default-link-disabled-bg;
                color: $navbar-default-link-disabled-color;
            }
            
            &:focus {
                background-color: $navbar-default-link-disabled-bg;
                color: $navbar-default-link-disabled-color;
            }
        }
        
        @media (max-width: $screen-xs-max) {
            /* Mobile only */
            
            &.open .dropdown-menu > li {
                & > a {
                    color: $navbar-default-link-color;
                    
                    &:hover {
                        background-color: $navbar-default-link-hover-bg;
                        color: $navbar-default-link-hover-color;
                    }
                    
                    &:focus {
                        background-color: $navbar-default-link-hover-bg;
                        color: $navbar-default-link-hover-color;
                    }
                }
                
                &.active > a {
                    background-color: $navbar-default-link-active-bg;
                    color: $navbar-default-link-active-color;
                    
                    &:hover {
                        background-color: $navbar-default-link-active-bg;
                        color: $navbar-default-link-active-color;
                    }
                    
                    &:focus {
                        background-color: $navbar-default-link-active-bg;
                        color: $navbar-default-link-active-color;
                    }
                }
            }
            
            &.open .dropdown-menu .divider {
                background-color: $navbar-default-border;
            }
        }
    }
    
    & .navbar-toggle {
        border-color: $navbar-default-toggle-border-color;
        
        & .icon-bar {
            background-color: $navbar-default-toggle-icon-bar-bg;
        }
        
        &:hover {
            background-color: $navbar-default-toggle-hover-bg;
            
            & .icon-bar {
                background-color: $navbar-default-toggle-icon-bar-hover-bg;
            }
        }
        
        &:focus {
            background-color: $navbar-default-toggle-hover-bg;
            
            & .icon-bar {
                background-color: $navbar-default-toggle-icon-bar-active-bg;
            }
        }
    }
    
    & .navbar-collapse,
    & .navbar-form {
        border-color: $navbar-default-border;
    }
}

